@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
header {
  position: fixed;
  top: 0;
  padding: 0 50px;
  background-color: var(--secondary-color);
  width: 100%;
  height: 100px;

  display: flex;
  align-items: center;
  z-index: 5;
}

nav {
  position: relative;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

button {
  padding: 0;
  border: none;
  outline: none;
  font: inherit;
  color: inherit;
  background: none;
}

.logo {
  cursor: pointer;
}

.logo img {
  height: 30px;
}
.navlinks {
  display: flex;
  align-items: center;
  justify-content: center;
}

nav ul {
  list-style-type: none;
  display: flex;
  align-items: center;
}

nav ul li {
  margin: 0 5px;
  padding: 10px;
}

.navlink {
  padding: 10px 0;
  color: var(--elements-color);
  font-size: 14px;
  transition: color 0.75s;
  position: relative;
  cursor: pointer;
}

.navlink::after {
  content: "";
  position: absolute;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.25s ease;
  transition: transform 0.25s ease;
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
  -webkit-transform-origin: left;
          transform-origin: left;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
}

.navlink:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  background-color: var(--primary-color);
}

.navlink:hover {
  color: var(--primary-color);
}

.hamburgerBtn {
  display: flex;
  justify-content: center;
  align-items: center;

  position: relative;
  z-index: 99;

  margin-right: -15px;
  padding: 15px;
  border: 0px;
  background-color: transparent;
  color: inherit;
  text-transform: none;
  transition-timing-function: linear;
  transition-duration: 0.15s;
  transition-property: opacity, -webkit-filter;
  transition-property: opacity, filter;
  transition-property: opacity, filter, -webkit-filter;
}

.ham-box {
  position: relative;
  height: 24px;
  width: 30px;

  display: none;
}

.ham-box-inner {
  height: 2px;
  width: 30px;
  border-radius: 4px;
  background-color: var(--primary-color);

  position: absolute;
  top: 50%;
  right: 0;

  transition: -webkit-transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s;

  transition: transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s;

  transition: transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s, -webkit-transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.ham-box-inner::before,
.ham-box-inner::after {
  content: "";
  position: absolute;
  right: 0;
  left: auto;
  height: 2px;
  border-radius: 4px;
  background-color: var(--primary-color);
}

.ham-box-inner::before {
  width: 100%;
  top: -10px;
  transition: top 0.1s ease-in 0.25s, opacity 0.1s ease-in;
}
.ham-box-inner::after {
  width: 100%;
  bottom: -10px;
  transition: bottom 0.1s ease-in 0.25s,
    -webkit-transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: bottom 0.1s ease-in 0.25s,
    transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: bottom 0.1s ease-in 0.25s,
    transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19),
    -webkit-transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.ham-box-inner.show {
  position: absolute;
  top: 50%;
  right: 0px;
  width: 30px;
  height: 2px;
  border-radius: 4px;
  background-color: var(--primary-color);
  transition: -webkit-transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
  transition: transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
  transition: transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s, -webkit-transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
}

.ham-box-inner.show::before {
  width: 100%;
  top: 0px;
  opacity: 0;
}

.ham-box-inner.show::after {
  width: 100%;
  bottom: 0px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

@media (max-width: 1080px) {
  header {
    padding: 0 40px;
  }
}
@media (max-width: 768px) {
  header {
    padding: 0 25px;
  }

  .navlinks {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    /* width: min(75vw, 400px); */
    width: 100vw;
    background-color: var(--secondary-color);
    opacity: 0;
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
    visibility: hidden;
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .navlinks.show {
    display: flex;
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateX(0vw);
            transform: translateX(0vw);
  }

  .ham-box {
    display: block;
  }

  nav ul {
    flex-direction: column;
  }

  nav ul li {
    margin: 0px auto 30px;
    width: 100%;
  }

  nav ul li a {
    display: inline-block;
    text-align: center;
    width: 100%;
    padding: 3px 20px 20px;
  }
}

main {
  margin-top: 100px;
  padding: 0 50px;
}

section {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 7rem 0;
}

h1 {
  font-size: clamp(40px, 8vw, 80px);
  color: #fff;
  margin: 5px 0 12px;
  line-height: 1.1;
}

h2 {
  font-size: clamp(24px, 4vw, 36px);
  color: var(--secondary-elements-color);
  line-height: 1.1;
}

.section-title {
  font-size: clamp(36px, 5vw, 48px);
  padding-bottom: 12px;
  margin-bottom: 30px;
  position: relative;
}

.section-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 8rem;
  height: 4px;
  background-color: var(--primary-color);
}

/* Hero Section */

.hero {
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero-span {
  font-size: 16px;
  color: var(--primary-color);
}

.social {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.social-icon {
  font-size: 30px;
  padding: 4px;
  margin-right: 20px;
  cursor: pointer;
  color: var(--secondary-elements-color);
}

.social-icon:hover {
  color: #fff;
}

/* About Section */

.about-description p {
  width: 50%;
  margin-bottom: 16px;
  font-size: 18px;
  color: var(--text-color);
}

.link {
  position: relative;
  color: var(--primary-color);
  cursor: pointer;
}

.link::after {
  content: "";
  position: absolute;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.25s ease;
  transition: transform 0.25s ease;
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
  -webkit-transform-origin: left;
          transform-origin: left;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
}

.link:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  background-color: var(--primary-color);
}

.link:hover {
  color: var(--primary-color);
}

/* Skills Section */

.skills-subsection {
  padding-bottom: 2rem;
}
.skills-subsection ul li {
  list-style-type: none;
  margin-right: 10px;
  padding: 1rem;

  width: 7rem;
  height: 7rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.skills-subsection h4 {
  text-align: center;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills-logo {
  height: 2rem;
  width: 2rem;
}

.skills-logo svg {
  fill: var(--primary-color);
}

/* Projects Section */

.projects-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

.project-cards {
  background-color: var(--secondary-color);
  padding: 2rem 1.75rem;
  box-shadow: 0 10px 30px -15px rgba(2, 27, 27, 0.7);

  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: space-between;
}

.project-title {
  font-size: 20px;
  margin-bottom: 12px;
}

.project-description {
  font-size: 14px;
  line-height: 1.4rem;
}

.techList {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.2 rem;
  gap: 0.2 rem;
  margin-top: auto;
}

.techList li {
  list-style-type: none;
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
}
.project-links {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.live-link {
  padding: 12px;
  text-decoration: none;
  background-color: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.5);
  border-radius: 6px;
  display: flex;
  align-items: center;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.live-link:hover {
  -webkit-transform: translateY(-0.25rem);
          transform: translateY(-0.25rem);
  color: rgba(255, 255, 255, 0.75);
  background-color: rgba(255, 255, 255, 0.1);
}

.live-link svg {
  height: 1.5rem;
  width: 1.5rem;
}

@media (max-width: 1080px) {
  main {
    padding: 0 40px;
  }

  .projects-wrapper {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}
@media (max-width: 768px) {
  main {
    padding: 0 25px;
  }

  .about-description p {
    width: 100%;
  }
}

:root {
  --bg-color: #0b0c10;
  --primary-color: #66fcf1;
  --secondary-color: #1f2833;
  --elements-color: #e6e6e6;
  --secondary-elements-color: #858383;
  --text-color: #b8b8b8;
}

html {
  box-sizing: border-box;
  width: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Inter", sans-serif;
  background-color: #0b0c10;
  background-color: var(--bg-color);
  color: #e6e6e6;
  color: var(--elements-color);
}

a {
  text-decoration: none;
}

footer {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;

  text-align: center;
  font-size: 12px;
  padding: 25px 0;
  position: relative;
}
